import * as THREE from "three";
let modal;
let modalButton;
let windowWidth;
let windowHeight;
let scene;
let camera;
let renderer;
let sphereVertices;
let points;
let pointsGeometry;
let pointsMaterial;
let pointPositions;
let audioContext;
let audioSource;
let audioBufferSource;
let audioAnalyser;
let audioCount;
window.addEventListener("load", () => {
    initDom();
    initThree();
    initAudio().then(data => {
        modalButton.classList.remove("is-Hidden");
        modalButton.addEventListener("click", () => {
            onClickModalButton(data);
        }, false);
        modalButton.addEventListener("touchstart", () => {
            onClickModalButton(data);
        }, false);
    });
});
const initDom = () => {
    modal = document.getElementById("modal");
    modalButton = document.getElementById("modal_button");
};
const initThree = () => {
    // 
    windowWidth = window.innerWidth;
    windowHeight = window.innerHeight;
    // scene 
    scene = new THREE.Scene();
    // camera 
    camera = new THREE.PerspectiveCamera(45, windowWidth / windowHeight, 0.1, 1000);
    camera.position.z = 45;
    // renderer 
    renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    // 
    renderer.setPixelRatio(window.devicePixelRatio);
    // CSSCanvas
    renderer.setClearColor(0x000000, 0.0);
    // Canvas
    renderer.setSize(windowWidth, windowHeight);
    // bodyCanvas
    document.body.appendChild(renderer.domElement);
    // 
    const sphereGeometry = new THREE.SphereGeometry(10, 24, 24);
    sphereVertices = sphereGeometry.attributes.position.array;
    // // 
    // pointPositions = new Float32Array(sphereVertices.length);
    // // vertexPosition = { x: ???, y: ???, z: ??? } ...
    // // x, y, z  pointPositions 
    // let a = 0;
    // for (let i = 0; i < sphereVertices.length; i++) {
    //   let vertexPosition = sphereVertices[i];
    //   pointPositions[a] = vertexPosition[0];
    //   pointPositions[a + 1] = vertexPosition.y;
    //   pointPositions[a + 2] = vertexPosition.z;
    //   a += 3;
    // }
    // geometry 
    pointsGeometry = new THREE.BufferGeometry();
    pointsGeometry.setAttribute("position", new THREE.BufferAttribute(sphereVertices, 3));
    // material 
    const vertexShader = document.getElementById("vertexShader");
    const fragmentShader = document.getElementById("fragmentShader");
    pointsMaterial = new THREE.ShaderMaterial({
        vertexShader: vertexShader.textContent,
        fragmentShader: fragmentShader.textContent
    });
    pointsMaterial.transparent = true;
    pointsMaterial.depthTest = false;
    // geometry  material 3D
    points = new THREE.Points(pointsGeometry, pointsMaterial);
    scene.add(points);
    // 1
    renderer.render(scene, camera);
};
const initAudio = () => new Promise(resolve => {
    // AudioContext 
    // @ts-ignore
    audioContext = new (window.AudioContext || window.webkitAudioContext)();
    // 
    audioSource = "/sound_effects/bgm1.wav";
    // 
    audioBufferSource = audioContext.createBufferSource();
    // 
    audioAnalyser = audioContext.createAnalyser();
    let request = new XMLHttpRequest();
    request.open("GET", audioSource, true);
    request.responseType = "arraybuffer";
    // 
    // 
    request.onload = () => {
        audioContext.decodeAudioData(request.response, buffer => resolve(buffer));
    };
    request.send();
});
const setAudio = (buffer) => {
    // 
    audioAnalyser.smoothingTimeConstant = 1.0;
    // fft
    audioAnalyser.fftSize = 2048;
    // 
    audioBufferSource.buffer = buffer;
    // 
    audioBufferSource.loop = true;
    // 
    audioCount = new Uint8Array(audioAnalyser.frequencyBinCount);
    // 
    audioBufferSource.connect(audioAnalyser);
    // 
    audioAnalyser.connect(audioContext.destination);
    // 
    audioBufferSource.start(0);
};
const playAudio = () => {
    // 
    audioAnalyser.getByteTimeDomainData(audioCount);
    // 
    let number = audioCount.reduce((a, b) => Math.max(a, b));
    // 0  255  0  1 
    number = number / 255;
    // 2()
    // 0.5 
    points.scale.x = Math.pow(number, 2) + 0.5;
    points.scale.y = Math.pow(number, 2) + 0.5;
    points.scale.z = Math.pow(number, 2) + 0.5;
    // y
    points.rotation.y += 0.0025;
    // 
    renderer.render(scene, camera);
    // 
    requestAnimationFrame(playAudio);
};
const onClickModalButton = (buffer) => {
    modal.classList.add("is-Hidden");
    setAudio(buffer);
    playAudio();
};
